*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p{
  font-size: 1.2rem;

}
h1{
  font-size: 6rem;
}

.app{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.5);
  color: black;
}
.app:before{
  content: '';
  background: url('./assets/bg1.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}
.app .search{
  text-align: center;
  padding: 1rem;
}
.app input{
  padding: .7rem 3.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(207, 244, 255, 0.8);
  background: rgba(255,255,255,0.1);
}
::placeholder{
  color: white;
}
.container{
  max-width: 700px;
  height: 600px;
  margin: auto;
  padding: 0 .7rem;
  position: relative;
  top: 6%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top{
  font-variant: small-caps;
  width: 100%;
  margin: 1rem auto;
  padding: 2rem;
  border-radius: 16px;
  background-color: rgba(206, 225, 247, 0.5);

}
 .app .description{
  font-variant: small-caps;
  position: relative;
  right: -95%;
  transform-origin: 0 0;
  transform: rotate(270deg);
 }
.app .desc{
  font-variant: small-caps;
}
.app .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(206, 225, 247, 0.5);
}
.bold{
  font-weight: 600;
}